/* NAVBAR */
.navbar-brand {
  font-family: "Pacifico";
  color: black;
  padding: 1%;
  font-size: 1.2rem;
}
/* NAVBAR ENDS */

/* WELCOME PAGE */
.dash {
  position: relative;
  font-family: Poppins;
  font-size: 2rem;
  line-height: 1.2em;
  color: black;
  font-weight: bolder;
  margin-bottom: 5%;
}
.dash:before {
  position: absolute;
  left: 0;
  top: 1.2em;
  height: 0;
  width: 10%;
  content: "";
  border-top: 5px solid #3083dc;
}

.label {
  font-family: Poppins;
  color: #4e4187;
  font-weight: bold;
}

.input,
.textarea {
  font-family: Quicksand;
  font-weight: bolder;
}

.socialicon {
  margin-right: 1%;
}

#cvbtn {
  font-family: "Poppins";
  background-color: #4e4187;
  color: white;
  width: 50%;
}
/* WELCOME PAGE ENDS*/

/* PROFILE PAGE */
.herohead {
  position: relative;
  font-family: Poppins;
  font-size: 2rem;
  line-height: 1.2em;
  color: black;
  font-weight: bolder;
  margin: 3%;
}

.herohead:before {
  position: absolute;
  left: 0;
  top: 1.2em;
  height: 0;
  width: 5%;
  content: "";
  border-top: 5px solid #3083dc;
}

.step-title {
  font-family: "Poppins";
  color: black;
  font-weight: bolder;
}
.step-desc {
  font-family: "Quicksand";
  color: black;
  font-weight: bolder;
}

.motto {
  margin-bottom: 5%;
}

.skill-icon {
  font-size: 3rem;
  color: black;
  margin-right: 3%;
}

.skill-name {
  color: #4e4187;
  font-family: "Poppins";
  font-weight: bolder;
  font-size: 1.1rem;
}

.skill-icon:hover {
  color: #3083dc;
}

/* PROFILE PAGE ENDS*/

/* PROJECT PAGE */
.tags {
  font-family: "Poppins";
}

#projectpg {
  height: 100vh;
}
.subtitle {
  font-family: Poppins;
  color: #3083dc;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  font-weight: bolder;
  font-size: 1.5rem;
}

.randombtn {
  font-family: "Poppins";
  background-color: #4e4187;
  color: white;
}

.proj-title {
  font-family: "Poppins";
  color: #4e4187;
  font-weight: bolder;
  font-size: 1.5rem;
  margin: 2%;
}

.proj-desc {
  font-family: "Poppins";
  font-weight: bold;
  font-size: 1.1rem;
  margin-bottom: 3%;
  padding-left: 5%;
  padding-right: 5%;
}

.myquote {
  font-size: 1.5rem;
  margin-bottom: 1%;
}

.qt-title {
  font-family: "Poppins";
  color: #4e4187;
  margin-bottom: 1%;
  font-size: 1.5rem;
  font-weight: bolder;
}

.qt-desc {
  font-family: "Poppins";
  font-size: 1.1rem;
  margin-bottom: 5%;
  padding-left: 5%;
  padding-right: 5%;
  font-weight: bold;
}
/* PROJECT PAGE ENDS*/
